    <va-button :color="'primary'" @click="goto('/')" style="width: 140px"> {{ 'Rechercher'.toUpperCase() }}</va-button>
    <span style="display: inline-block; width: 5px"> </span>
    <va-button :color="'primary'" @click="goto('/train')" style="width: 140px"> {{ 'Entraîner'.toUpperCase() }}
    </va-button>
  </div>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>

import {getAPiKey} from "@/services/const";
import $ from "jquery";

export default {
  name: "App",
  async mounted() {

   const apiKey = getAPiKey();

   if(!apiKey){
      this.$vaToast.init({ message: 'APiKey Incorrect ou manquant', color: 'danger', duration: 70000000 });
     $(".app").remove();

   }
   else{
    // sessionStorage.setItem('apiKey', apiKey.value)
   }

  },
  data(){
    return {
      apiKey: getAPiKey()
    }
  },
  methods: {
    goto(path) {
      this.$router.push(path)
    },
  }
};
</script>

